import { Currency } from '../Currency'
import { NativeCurrency } from '../NativeCurrency'
import { Token } from '../Token'
import { WNATIVE } from '../../constants/tokens'
import invariant from 'tiny-invariant'

export class Rails extends NativeCurrency {
  protected constructor(chainId: number) {
    super(chainId, 18, 'STMX', 'STMX')
  }

  public get wrapped(): Token {
    const wrails = WNATIVE[this.chainId]
    invariant(!!wrails, 'WRAPPED')
    return wrails
  }

  private static _cache: { [chainId: number]: Rails } = {}

  public static onChain(chainId: number): Rails {
    return this._cache[chainId] ?? (this._cache[chainId] = new Rails(chainId))
  }

  public equals(other: Currency): boolean {
    return other.isNative && other.chainId === this.chainId
  }
}
